import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Home',
    component: () => import('@/views/DragVerify')
  }, {
    path: '/',
    name: 'drag',
    component: () => import('@/views/DragVerify')
  }]
})



